@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Lilita+One&family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lilita+One&display=swap);
body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.app-body{contain:content}
.header{height:auto;min-height:5em;width:100%;position:-webkit-sticky;position:sticky;top:0px;display:flex;align-items:flex-start;z-index:1}@media screen and (max-width: 768px){.header{height:80px}}.strip{position:relative;background-color:rgba(56,47,41,.95);border-bottom:solid 1px rgba(56,47,41,.95);-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);height:100%;width:100%;display:flex;padding-left:12vw;justify-content:flex-end;box-shadow:0px 0px 5px rgba(0,0,0,.8)}@media screen and (min-width: 768px)and (max-width: 1200px){.strip{padding-left:18vw;justify-content:space-between}}.badge{position:absolute;z-index:5;height:120%;width:auto;background-color:#fff;left:15px;top:15px;border-radius:100%;border:8px double #382f29;box-shadow:rgba(56,47,41,.8) 0 0 0 6px,rgba(0,0,0,.8) 0px 6px 5px;contain:content}@media screen and (max-width: 768px){.badge{background:none;border:none;box-shadow:none;border-radius:0;left:20px;top:0px;width:auto;height:80px;display:flex;align-items:center}}@media screen and (min-width: 768px){.badge img.vertical{height:100%;width:auto;-webkit-transform:translateY(-5%);transform:translateY(-5%)}.badge img.horizontal{display:none}}@media screen and (max-width: 768px){.badge img.horizontal{display:block;height:50%;width:auto;-webkit-transform:none;transform:none}.badge img.vertical{display:none}}.menu-items-container{display:flex;justify-content:space-around;align-items:center;width:100%;height:100%;max-width:1024px}@media screen and (min-width: 768px)and (max-width: 1200px){.menu-items-container{justify-content:flex-end}}@media screen and (max-width: 768px){.menu-items-container{border:none}}.menu-items-container .menu-item{font-size:x-large;font-family:"Montserrat",sans-serif;font-weight:medium;color:rgba(255,255,255,.6);transition:ease-in-out .2s;height:100px}@media screen and (min-width: 768px)and (max-width: 1200px){.menu-items-container .menu-item{font-size:large;padding-right:32px;padding-left:32px}}@media screen and (max-width: 768px){.menu-items-container .menu-item{display:none}}.menu-button{display:none;transition:ease-in-out .2s;-webkit-transform:rotate(0deg);transform:rotate(0deg)}@media screen and (max-width: 768px){.menu-button{display:block}}
.home-container{text-align:center}
.events-container{text-align:center}
.devchamps-background{height:100%;min-height:100vh;width:100%;background-image:url(/static/media/devchamps-bg.288b713c.jpg);background-size:cover;background-position:center;background-repeat:no-repeat;display:grid;justify-items:center;align-items:center}.devchamps-container{height:100%;width:45%;min-width:400px;display:grid;grid-template-rows:15vw 1fr auto;border-color:#efe7d8;border-width:1em;border-left-style:solid;border-right-style:solid}.devchamps-heading{background-color:#b34744;height:100%;width:100%;display:grid;grid-template-columns:30% 70%}.ashu{margin:1.5em;background-image:url(/static/media/ashu-cool.e688a890.gif);background-size:contain;background-position:center;background-repeat:no-repeat}.title-grid{display:grid;grid-template-rows:50% 50%}.title{height:100%;width:100%;background-image:url(/static/media/devchamps-heading.6fb618f6.png);background-size:contain;background-position:center;background-repeat:no-repeat}.devchamps-container>.devchamps-heading>.devchamps-ashu{min-width:0;min-height:0;height:100%;-webkit-transform:scale(0.85);transform:scale(0.85)}.faq{text-align:center;color:#efe7d8;font-family:"Lilita One",cursive;font-size:5em}.faq-container{background-color:#efe7d8;font-family:"Montserrat";display:grid;justify-items:center;align-items:center}.accordions{margin:1em}.devchamps-container>.faq-container .row-title-text{margin:5px;margin-left:20px;font-weight:900;color:#5f394b !important}.devchamps-container>.faq-container .row-title-text:active{color:#db8263 !important}.devchamps-container>.faq-container .row-content-text{margin-left:15px;margin-right:15px;line-height:1.5rem;text-align:left}.devchamps-container>.faq-container .row-content-text em{font-weight:800}.bottom{background-color:#799aad;height:100%;width:100%}.hof-coming-soon{font-family:"Bebas Neue";color:#efe7d8;font-size:3.5rem;text-align:center}@media screen and (max-width: 820px){.devchamps-container{grid-template-rows:25vw 1fr auto}.hof-coming-soon{font-size:2rem}}@media screen and (max-width: 420px){.devchamps-container{grid-template-rows:32vw 1fr auto;border-width:0px}.ashu{margin:.5em}.faq{font-size:2.3em}.accordions{margin:.4em 1em .4em 1em}.hof-coming-soon{font-size:1rem}}
.contact-container{text-align:center}
.page-not-found-container{text-align:center;background-color:#fff;min-height:80vh;display:flex;flex-direction:column;align-items:center;justify-content:center;color:#000}
