@import url("https://fonts.googleapis.com/css2?family=Lilita+One&display=swap");

.devchamps-background {
  height: 100%;
  min-height: 100vh;
  width: 100%;
  background-image: url(../resources/images/devchamps/devchamps-bg.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: grid;
  justify-items: center;
  align-items: center;
}

.devchamps-container {
  height: 100%;
  width: 45%;
  min-width: 400px;
  display: grid;
  grid-template-rows: 15vw 1fr auto;
  border-color: #efe7d8;
  border-width: 1em;
  border-left-style: solid;
  border-right-style: solid;
}

.devchamps-heading {
  background-color: #b34744;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 30% 70%;
}

.ashu {
  margin: 1.5em;
  background-image: url(../resources/images/devchamps/ashu-cool.gif);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.title-grid {
  display: grid;
  grid-template-rows: 50% 50%;
}

.title {
  height: 100%;
  width: 100%;
  background-image: url(../resources/images/devchamps/devchamps-heading.png);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.devchamps-container > .devchamps-heading > .devchamps-ashu {
  min-width: 0;
  min-height: 0;
  height: 100%;
  transform: scale(0.85);
}

.faq {
  text-align: center;
  color: #efe7d8;
  font-family: "Lilita One", cursive;
  font-size: 5em;
}

.faq-container {
  background-color: #efe7d8;
  font-family: "Montserrat";
  display: grid;
  justify-items: center;
  align-items: center;
}

.accordions {
  margin: 1em;
}

.devchamps-container > .faq-container .row-title-text {
  margin: 5px;
  margin-left: 20px;
  font-weight: 900;
  color: #5f394b !important;
}

.devchamps-container > .faq-container .row-title-text:active {
  color: #db8263 !important;
}

.devchamps-container > .faq-container .row-content-text {
  margin-left: 15px;
  margin-right: 15px;
  line-height: 1.5rem;
  text-align: left;
}

.devchamps-container > .faq-container .row-content-text em {
  font-weight: 800;
}

.bottom {
  background-color: #799aad;
  height: 100%;
  width: 100%;
}

.hof-coming-soon {
  font-family: "Bebas Neue";
  color: #efe7d8;
  font-size: 3.5rem;
  text-align: center;
}

@media screen and (max-width: 820px) {
  .devchamps-container {
    grid-template-rows: 25vw 1fr auto;
  }

  .hof-coming-soon {
    font-size: 2rem;
  }
}

@media screen and (max-width: 420px) {
  .devchamps-container {
    grid-template-rows: 32vw 1fr auto;
    border-width: 0px;
  }

  .ashu {
    margin: 0.5em;
  }

  .faq {
    font-size: 2.3em;
  }

  .accordions {
    margin: 0.4em 1em 0.4em 1em;
  }

  .hof-coming-soon {
    font-size: 1rem;
  }
}
